import { Component, EventEmitter, OnInit, OnDestroy, Output, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Router } from '@angular/router';
import { MatMenuModule } from '@angular/material/menu';
import { MatButtonModule } from '@angular/material/button';
import { AppInputComponent, IconComponent, CoreEventService, AuthService } from '@lk/core';

// Define local type for breadcrumb items (matches BreadcrumbItem from @lk/core)
type BreadcrumbItem = {
  label: string;
  route?: string;
  icon?: string;
};
import { NotificationService } from '../notification/notification.service';

@Component({
  selector: 'app-topbar',
  standalone: true,
  imports: [
    CommonModule,
    MatMenuModule,
    MatButtonModule,
    AppInputComponent,
    IconComponent
  ],
  templateUrl: './topbar.component.html',
  styleUrl: './topbar.component.scss'
})
export class TopbarComponent implements OnInit, OnDestroy {
  searchText: string = '';
  @Output() openPatientQueue = new EventEmitter<any>();

  breadcrum: Array<{ title: string; url?: string; icon?: string }> = [];

  private breadcrumbUnsubscribe?: () => void;

  constructor(
    private notificationService: NotificationService,
    private router: Router,
    private authService: AuthService,
    private eventService: CoreEventService
  ) {}

  onNotificationClick(event: Event) {
    const targetElement = event.currentTarget as HTMLElement;
    const notifications = this.notificationService.getSampleNotifications();
    this.notificationService.openNotificationPanel(notifications, targetElement);
  }

  ngOnInit() {
    // Use signal-based breadcrumb from CoreEventService
    // Use the service's onBreadcrumbChange method which handles injection context properly
    this.breadcrumbUnsubscribe = this.eventService.onBreadcrumbChange((breadcrumb) => {
      if (breadcrumb && Array.isArray(breadcrumb) && breadcrumb.length > 0) {
        // Convert BreadcrumbItem[] to the format expected by the template
        this.breadcrum = breadcrumb.map((item) => ({
          title: item.label,
          url: item.route,
          icon: item.icon
        }));
      } else {
        // Clear breadcrumb if null or empty
        this.breadcrum = [];
      }
    });
  }

  ngOnDestroy() {
    // Clean up the breadcrumb subscription
    if (this.breadcrumbUnsubscribe) {
      this.breadcrumbUnsubscribe();
    }
  }

  showPatientQueue(event: Event) {
    this.openPatientQueue.emit(event);
  }

  onProfileClick(event: Event) {
    // This method is now handled by the mat-menu trigger
    console.log('Profile menu opened');
  }

  onProfileMenuClick() {
    this.router.navigate(['/profile']);
  }

  onSettingsMenuClick() {
    this.router.navigate(['/settings']);
  }

  onLogoutClick() {
    this.authService.logout();
  }

  onBreadcrumbClick(item: { title: string; url?: string; icon?: string }) {
    if (item.url) {
      this.router.navigate([item.url]);
    }
  }
}
