import { CommonModule } from '@angular/common';
import { Component, Input, ContentChild, TemplateRef } from '@angular/core';
import { IconComponent } from '@lk/core';

export interface BreadcrumbItem {
  label: string;
  route?: string;
  icon?: string;
  isActive?: boolean;
}

@Component({
  selector: 'app-page',
  standalone: true,
  imports: [CommonModule, IconComponent],
  template: `
    <div class="page-container">
      <div class="page-body" [attr.page-body]="true">
        <ng-container *ngIf="pageBodyTemplate; else defaultBody">
          <ng-container *ngTemplateOutlet="pageBodyTemplate"></ng-container>
        </ng-container>
        <ng-template #defaultBody>
          <ng-content select="[page-body]"></ng-content>
        </ng-template>
      </div>
      <ng-container *ngIf="isFooter">
        <div class="page-footer" [attr.footer]="true">
          <ng-container *ngIf="footerTemplate; else defaultFooter">
            <ng-container *ngTemplateOutlet="footerTemplate"></ng-container>
          </ng-container>
          <ng-template #defaultFooter>
            <ng-content select="[footer]"></ng-content>
          </ng-template>
        </div>
      </ng-container>
    </div>
  `,
  styles: [`
    .page-container {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
    .page-body {
      flex: 1;
      width: 100%;
    }
    .page-footer {
      width: 100%;
    }
  `]
})
export class PageComponent {
  @Input() breadcrumb: BreadcrumbItem[] | null = null;
  @Input() isFooter = false;

  @ContentChild('pageBody') pageBodyTemplate?: TemplateRef<any>;
  @ContentChild('footer') footerTemplate?: TemplateRef<any>;
}
